const BASE = "http://localhost:5000";

// Grsel URL'lerini backend base URL ile birletiren utility fonksiyonu
export const getImageUrl = (imagePath: string): string => {
  if (!imagePath) return "";
  let p = imagePath.trim();
  if (!p) return "";

  // http veya data URL ise direkt dndr
  if (/^(https?:)?\/\//i.test(p) || p.startsWith("data:")) return p;

  // Windows backslash -> forward slash
  p = p.replace(/\\/g, "/");

  // Bata tekrarl uploads neklerini tekilletir (uploads/ ... bir veya daha fazla tekrar)
  p = p.replace(/^\/?(?:uploads\/)+/, "/uploads/");

  // Eer hala /uploads/ ile balamyorsa ekle
  if (!p.startsWith("/uploads/")) {
    // Bataki / iaretlerini krpp /uploads/ ekle
    p = "/uploads/" + p.replace(/^\/+/, "");
  }

  return `${BASE}${p}`;
};

// Fallback grsel URL'i
export const getFallbackImageUrl = (): string => {
  return "/placeholder-image.jpg";
};

// Grsel URL'inin geerli olup olmadn kontrol eden fonksiyon
export const isValidImageUrl = (imagePath: string): boolean => {
  return !!(imagePath && imagePath.trim() !== "");
};
