import React, { useState, useEffect } from "react";
import { useNavigate } from "react-router-dom";
import BannerForm from "../../components/intro-banners/BannerForm";
import { api } from '../../utils/api';  // axiosInstance yolunu kendine gre ayarla
import Swal from "sweetalert2";
import { useBreadcrumb } from "../../contexts/BreadcrumbContext";

interface Banner {
  imageFile: File | null;
  order_index: number;
  title_line1: string;
  title_line2: string;
  button_text: string;
  button_link: string;
}

interface IntroBanner {
  id: number;
  image: string;           // Mevcut grsel URL'si
  title_line1: string;
  title_line2: string;
  button_text: string;
  button_link: string;
  order_index: number;
}

export default function NewIntroBannerPage() {
  const [banner, setBanner] = useState<Banner>({
    imageFile: null,
    order_index: 1,
    title_line1: "",
    title_line2: "",
    button_text: "",
    button_link: "",
  });

  const navigate = useNavigate();
  const [banners, setBanners] = useState<IntroBanner[]>([]);
  const { setBreadcrumbs } = useBreadcrumb();

  useEffect(() => {
    // Breadcrumb' ayarla
    setBreadcrumbs([
      { name: "Dashboard", to: "/admin/dashboard" },
      { name: "Intro Banners", to: "/admin/intro-banners" },
      { name: "New Banner" }
    ]);
  }, [setBreadcrumbs]);

  function findFirstEmptyIndex(banners: IntroBanner[]): number {
    const max = 3; // Maks banner says
    const usedIndexes = banners.map(b => b.order_index);
    for (let i = 1; i <= max; i++) {
      if (!usedIndexes.includes(i)) return i;
    }
    return max + 1; // Tm indexler doluysa (teorik olarak olmamal)
  }

  useEffect(() => {
    axiosInstance.get<IntroBanner[]>("/api/intro-banners")
    .then(res => {
      setBanners(res.data);
    })
    .catch(() => setBanners([]));
  }, []);

  useEffect(() => {
    const nextIndex = findFirstEmptyIndex(banners);
    setBanner(prev => ({ ...prev, order_index: nextIndex }));
  }, [banners]);

  const handleFileChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    if (e.target.files && e.target.files[0]) {
      setBanner(prev => ({ ...prev, imageFile: e.target.files![0] }));
    }
  };

  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value } = e.target;
    setBanner(prev => ({
      ...prev,
      [name]: value,
    }));
  };

  const handleSubmit = () => {
    if (!banner.imageFile) {
      Swal.fire({
        icon: "warning",
        title: "Uyar",
        text: "Ltfen bir grsel sein!",
      });
      return;
    }

    if (banner.order_index === 3) {
      if (
        !banner.title_line1.trim() ||
        !banner.button_text.trim() ||
        !banner.button_link.trim()
      ) {
        Swal.fire({
          icon: "warning",
          title: "Uyar",
          text: "Ltfen 3. banner iin tm alanlar doldurun!",
        });
        return;
      }
    }

    const formData = new FormData();
    formData.append("image", banner.imageFile);
    formData.append("order_index", String(banner.order_index));

    if (banner.order_index === 3) {
      formData.append("title_line1", banner.title_line1);
      formData.append("title_line2", banner.title_line2);
      formData.append("button_text", banner.button_text);
      formData.append("button_link", banner.button_link);
    }

    axiosInstance.post("/api/intro-banners", formData, {
      headers: {
        "Content-Type": "multipart/form-data",
      },
    })
    .then(() => {
      Swal.fire({
        icon: "success",
        title: "Baarl",
        text: "Banner baaryla eklendi",
        timer: 2000,
        showConfirmButton: false,
      });
      navigate("/admin/intro-banners");
    })
    .catch(err =>
      Swal.fire({
        icon: "error",
        title: "Hata",
        text: err.message,
      })
    );
  };

  return (
    <div className="p-6 max-w-xl mx-auto">
      <h1 className="text-2xl font-bold mb-4">Yeni Intro Banner Ekle</h1>
      <BannerForm
        banner={banner}
        onChange={handleChange}
        onFileChange={handleFileChange}
        onSubmit={handleSubmit}
        submitText="Ekle"
        showFullFields={banner.order_index === 3}
      />
    </div>
  );
}
